<template>
  <Card class="card">
    <Breadcrumb>
      <BreadcrumbItem>船舶中心</BreadcrumbItem>
      <BreadcrumbItem>修改船舶信息</BreadcrumbItem>
    </Breadcrumb>
    <div class="box-container">
      <Collapse v-model="value1" accordion>
        <Panel name="1">
           <span>船舶信息</span>
          <Row slot="content" class="row">
              <Col span="2">船名</Col>
              <Col span="6">{{result.shipnameCn ? result.shipnameCn : "-"}}</Col>
              <Col span="2">mmsi</Col>
              <Col span="2">{{result.mmsi ? result.mmsi : "-"}}</Col>
          </Row>
           <Row slot="content" class="row">
              <Col span="2">船东</Col>
              <Col span="6">{{result.contactName ? result.contactName : "-"}}</Col>
              <Col span="2">手机号</Col>
              <Col span="6">{{result.contactPhone ? result.contactPhone : "-"}}</Col>
          </Row>
           <Row slot="content" class="row">
              <Col span="2">船长</Col>
              <Col span="6"> <Input placeholder="请输入船长" style="width:220px" v-model="long" /> </Col>
              <Col span="2">船宽</Col>
              <Col span="6"><Input placeholder="请输入船宽" style="width:220px"  v-model="width" /></Col>
          </Row>
           <Row slot="content" class="row">
              <Col span="2">载货吨位</Col>
              <Col span="6"><Input placeholder="请输入载货吨位" style="width:220px" v-model="loadTons" /></Col>
              <Col span="2">TPC</Col>
              <Col span="6"><Input placeholder="请输入TPC" style="width:220px"  v-model="tpc" /></Col>
          </Row>
           <Row slot="content" class="row">
              <Col span="2">证书吨位</Col>
              <Col span="6"><Input placeholder="请输入证书吨位" style="width:220px"  v-model="certTons"/></Col>
              <Col span="2">每公里油耗</Col>
              <Col span="6"><Input placeholder="请输入每公里油耗" style="width:220px"  v-model="oilF"/></Col>
          </Row>
           <!-- <Row slot="content" class="row">
              <Col span="2">历史航线</Col>
              <Col span="10">
                 <span v-for="(list,i) in hisLine" :key="i">{{list}}</span>
              </Col>
          </Row> -->
          <Row slot="content" class="row">
            <Divider/>
          </Row>
           <Row slot="content" class="row">
               <Button type="primary" @click="save">保存</Button>
          </Row>
        </Panel>
      </Collapse>
    </div>
  </Card>
</template>
<script>
   export default {
        data () {
            return {
                value1: '1',
                value2:'2',
                result:'',
                hisLine:"",
                long:'',
                width:'',
                loadTons:'',
                tpc:"",
                certTons:'',
                oilF:''
            }
        },
         methods:{
         async  shipDetail(){
              let data  = {
                shipCode:this.$route.query.code
              }
             try{
                   let res = await this.$http.shipDetail(data)
                   if(0==res.data.status ){
                     this.result = res.data.result
                     let hisLine = res.data.result.historyRoute
                     if(res.data.result.historyRoute){
                       let newLine =   hisLine.slice(1,hisLine.length-1)
                       let endLine = newLine.split(',')
                       this.hisLine = endLine
                     }else{
                        this.hisLine  = ''
                     }

                     this.long = res.data.result.length
                     this.width = res.data.result.breadth
                     this.loadTons = res.data.result.loadTons
                     this.tpc = res.data.result.tpc
                     this.certTons = res.data.result.certTons
                     this.oilF = res.data.result.fuelConsumptionPerKm
                   }

              }
              catch(err){
                this.$Notice.error({
                  desc:'服务器错误,请稍后重试.'
                })
                throw Error(err)

              }

           },
         async save(){
            let data = {
                shipCode:this.$route.query.code,
                length:this.long,
                breadth:this.width,
                loadTons:this.loadTons,
                tpc:this.tpc,
                certTons:this.certTons,
                fuelConsumptionPerKm:this.oilF,
            }
           try{

                  let res = await this.$http.updateShip(data)
                   if(!res.data.status){
                    this.$Notice.success({
                        desc:"保存成功"
                    })
                    setTimeout(()=>{
                        const {href} = this.$router.resolve({
                            path:'/shipCenter/shipdetail',
                            query:{
                                code:this.$route.query.code
                            }
                         });
                         window.open(href, '_blank')
                    },1300)
                   }
                   else{
                        this.$Notice.error({
                        desc:res.data.msg
                    })
                    return false
                   }
               }
               catch(err){
                    this.$Notice.error({
                    desc:'服务器错误,请稍后重试.'
                })
                console.log(err)
               }

           }
        },
        created(){
          this.shipDetail()
        }
    }
</script>
<style lang="scss" scoped>
.table-row{
  margin: 20px 0px;
}
.ivu-row{
    padding: 0 30px;
    margin: 10px 0;
}

</style>



